import React from "react";
import unixToLocal from "../utils/unixtolocal";
import { ReactComponent as Success } from "../assets/success.svg";
import { ReactComponent as Recovered } from "../assets/recovered.svg";
import { ReactComponent as Reused } from "../assets/reused.svg";
import { ReactComponent as Failed } from "../assets/failed.svg";

import styles from "../styles/LaunchCard.module.css";

const LaunchCard = ({ launchData }) => {
  return (
    <div className={styles.launch__card}>
      <div>
        <div>
          <div className={styles.launch__card__name}>{launchData.name}</div>
          <div className={styles.launch__card__date}>
            {unixToLocal(launchData.date_unix)}
          </div>
          <div className={styles.launch__card__details}>
            {launchData.details}
          </div>
        </div>
        <div className={styles.launch__card__status}>
          <div>Status</div>
          <div className={styles.launch__card__statusLogo}>
            {launchData.success ? (
              <div className={styles.toolTip}>
                <Success />
                <div className={styles.toolTipText}>Success</div>
              </div>
            ) : (
              <div className={styles.toolTip}>
                <Failed />
                <div className={styles.toolTipText}>Failed</div>
              </div>
            )}
            {launchData.fairings && (
              <>
                {launchData.fairings.recovered && (
                  <div className={styles.toolTip}>
                    <Recovered />
                    <div className={styles.toolTipText}>Recovered</div>
                  </div>
                )}
                {launchData.fairings.reused && (
                  <div className={styles.toolTip}>
                    <Reused />
                    <div className={styles.toolTipText}>Reused</div>
                  </div>
                )}
              </>
            )}
          </div>
        </div>
      </div>
      <div>
        <img
          className={styles.launch__card__patch}
          src={launchData.links.patch.small}
          alt="Mission Patch"
        />

        <div className={styles.launch__card__number}>
          #{launchData.flight_number}
        </div>
      </div>
    </div>
  );
};

export default LaunchCard;
